d, and collapsed rows.\n@mixin grid-row($behavior: false) {\n\n  // use @include grid-row(nest); to include a nested row\n  @if $behavior == nest {\n    width: auto;\n    margin-#{$default-float}: -($column-gutter/2);\n    margin-#{$opposite-direction}: -($column-gutter/2);\n    margin-top: 0;\n    margin-bottom: 0;\n    max-width: none;\n  }\n\n  // use @include grid-row(collapse); to collapsed a container row margins\n  @else if $behavior == collapse {\n    width: 100%;\n    margin: 0;\n    max-width: $row-width;\n  }\n\n  // use @include grid-row(nest-collapse); to collapse outer margins on a nested row\n  @else if $behavior == nest-collapse {\n    width: auto;\n    margin: 0;\n    max-width: none;\n  }\n\n  // use @include grid-row; to use a container row\n  @else {\n    width: 100%;\n    margin-#{$default-float}: auto;\n    margin-#{$opposite-direction}: auto;\n    margin-top: 0;\n    margin-bottom: 0;\n    max-width: $row-width;\n  }\n\n  // Clearfix for all rows\n  @include clearfix();\n\n}\n\n\n// For creating columns - @include these inside a media query to control small vs. large grid layouts\n@mixin grid-column($columns:false, $last-column:false, $center:false, $offset:false, $push:false, $pull:false, $collapse:false, $float:true) {\n\n  position: relative;\n\n  // If collapsed, get rid of gutter padding\n  @if $collapse {\n    padding-left: 0;\n    padding-right: 0;\n  }\n\n  // Gutter padding whenever a column isn't set to collapse\n  // (use $collapse:null to do nothing)\n  @else if $collapse == false {\n    padding-left: $column-gutter / 2;\n    padding-right: $column-gutter / 2;\n  }\n\n  // If a column number is given, calculate width\n  @if $columns {\n    width: gridCalc($columns, $total-columns);\n\n    // If last column, float naturally instead of to the right\n    @if $last-column { float: $opposite-direction; }\n  }\n\n  // If offset, calculate appropriate margins\n  @if $offset { margin-#{$default-float}: gridCalc($offset, $total-columns); }\n\n  // Source Ordering, adds left/right depending on which you use.\n  @if $push { #{$default-float}: gridCalc($push, $total-columns); #{$opposite-direction}: auto; }\n  @if $pull { #{$opposite-direction}: gridCalc($pull, $total-columns); #{$default-float}: auto; }\n\n  // If centered, get rid of float and add appropriate margins\n  @if $center {\n    margin-#{$default-float}: auto;\n    margin-#{$opposite-direction}: auto;\n    float: none !important;\n  }\n\n  @if $float {\n    @if $float == left or $float == true { float: $default-float; }\n    @else if $float == right { float: $opposite-direction; }\n    @else { float: none; }\n  }\n\n}\n\n\n@if $include-html-grid-classes != false {\n\t/* Grid HTML Classes */\n  .row {\n    @include grid-row;\n\n    &.collapse {\n      .column,\n      .columns { @include grid-column($collapse:true); }\n    }\n\n    .row { @include grid-row($behavior:nest);\n      &.collapse { @include grid-row($behavior:nest-collapse); }\n    }\n  }\n\n  .column,\n  .columns { @include grid-column($columns:$total-columns); }\n\n  @media only screen {\n\n    .column,\n    .columns { @include grid-column($columns:false); }\n\n    @for $i from 1 through $total-columns {\n      .small#{-$i} { @include grid-column($columns:$i,$collapse:null,$float:false); }\n    }\n\n    @for $i from 0 through $total-columns - 2 {\n      .small-offset-#{$i} { @include grid-column($offset:$i, $collapse:null,$float:false); }\n    }\n\n    [class*=\"column\"] + [class*=\"column\"]:last-child { float: $opposite-direction; }\n    [class*=\"column\"] + [class*=\"column\"].end { float: $default-float; }\n\n    .column.small-centered,\n    .columns.small-centered { @include grid-column($center:true, $collapse:null, $float:false); }\n  }\n\n  /* Styles for screens that are atleast 768px; */\n  @media #{$small} {\n\n    @for $i from 1 through $total-columns {\n      .large#{-$i} { @include grid-column($columns:$i,$collapse:null,$float:false); }\n    }\n\n    @for $i from 0 through $total-columns - 1 {\n      .row .large-offset-#{$i} { @include grid-column($offset:$i, $collapse:null,$float:false); }\n    }\n\n    @for $i from 1 through $total-columns - 1 {\n      .push#{-$i} { @include grid-column($push:$i, $collapse:null, $float:false); }\n      .pull#{-$i} { @include grid-column($pull:$i, $collapse:null, $float:false); }\n    }\n\n    .column.large-centered,\n    .columns.large-centered { @include grid-column($center:true, $collapse:null, $float:false); }\n\n    .column.large-uncentered,\n    .columns.large-uncentered {\n      margin-#{$default-float}: 0;\n      margin-#{$opposite-direction}: 0;\n      float: $default-float !important;\n    }\n\n    .column.large-uncentered.opposite,\n    .columns.large-uncentered.opposite {\n      float: $opposite-direction !important;\n    }\n\n\n  }\n\n}\n","//\n// Foundation Visibility Classes\n//\n$include-html-visibility-classes: $include-html-classes !default;\n\n@if $include-html-visibility-classes != false {\n\n  /* Foundation Visibility HTML Classes */\n  .show-for-small,\n  .show-for-medium-down,\n  .show-for-large-down { display: inherit !important; }\n  \n  .show-for-medium,\n  .show-for-medium-up,\n  .show-for-large,\n  .show-for-large-up,\n  .show-for-xlarge { display: none !important; }\n  \n  .hide-for-medium,\n  .hide-for-medium-up,\n  .hide-for-large,\n  .hide-for-large-up,\n  .hide-for-xlarge { display: inherit !important; }\n  \n  .hide-for-small,\n  .hide-for-medium-down,\n  .hide-for-large-down { display: none !important; }\n  \n  /* Specific visilbity for tables */\n  table {\n    &.show-for-small,\n    &.show-for-medium-down,\n    &.show-for-large-down,\n    &.hide-for-medium,\n    &.hide-for-medium-up,\n    &.hide-for-large,\n    &.hide-for-large-up,\n    &.hide-for-xlarge { display: table; }\n  }\n  thead {\n    &.show-for-small,\n    &.show-for-medium-down,\n    &.show-for-large-down,\n    &.hide-for-medium,\n    &.hide-for-medium-up,\n    &.hide-for-large,\n    &.hide-for-l